  const storage = {
    isDark: false,
  };

  const { L } = window;
  const mapContainer = document.getElementById('map');
  if (L && mapContainer) {
    let filterColor = [
      'bright:101%',
      'contrast:101%',
      'hue:23deg',
      'saturate:225%',
    ];
    let tileLayerTheme =
      'https://{s}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}{r}.png';
    let tiles;
    if (storage.isDark) {
      filterColor = [
        'invert:98%',
        'grayscale:69%',
        'bright:89%',
        'contrast:111%',
        'hue:205deg',
        'saturate:1000%',
      ];
    }

    if (window.is.ie()) {
      if (storage.isDark) {
        tileLayerTheme =
          'https://{s}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}{r}.png';
      }
      tiles = L.tileLayer(tileLayerTheme, {
        attribution: null,
        transparent: true,
      });
    } else {
      tiles = L.tileLayer.colorFilter(tileLayerTheme, {
        attribution: null,
        transparent: true,
        filter: filterColor,
      });
    }

    const map = L.map('map', {
      center: L.latLng(10.737, 0),
      zoom: 0,
      layers: [tiles],
      minZoom: 1.3,
      zoomSnap: 0.5,
      dragging: !L.Browser.mobile,
      tap: !L.Browser.mobile,
    });

    const mcg = L.markerClusterGroup({
      chunkedLoading: false,
      spiderfyOnMaxZoom: false,
    });

    points.map((point) => {
      const { name, location, street } = point;
      const icon = L.icon({
        iconUrl: `data:image/png;base64,
        `,
      });
      const marker = L.marker(
        new L.LatLng(point.lat, point.long),
        { icon },
        {
          name: name,
          location: location,
        }
      );
      const popupContent = `
        <h6 class="mb-1">${name}</h6>
        <p class="m-0 text-500">${street}, ${location}</p>
      `;
      const popup = L.popup({ minWidth: 180 }).setContent(popupContent);
      marker.bindPopup(popup);
      mcg.addLayer(marker);
      return true;
    });
    map.addLayer(mcg);
  }
